import { Dialog, Transition } from '@headlessui/react'
import { Fragment, useContext, } from 'react'
import { AppContext } from '@context/AppContext';

// Ref: https://headlessui.com/react/dialog#styling-the-dialog
export default function ImageModal(
) {
    const { isOpenModal, setIsOpenModal, selectedImage, setSelectedImage } = useContext(AppContext);

    async function closeModal() {
        setIsOpenModal(false);
        setSelectedImage(null);
    }

    // function openModal() {
    //     setIsOpenModal(true)
    // }

    return (
        <>
            <Transition appear show={isOpenModal} as={Fragment}>
                <Dialog as="div" className="relative z-10" onClose={closeModal}>
                    <Transition.Child
                        as={Fragment}
                        enter="ease-out duration-300"
                        enterFrom="opacity-0"
                        enterTo="opacity-100"
                        leave="ease-in duration-200"
                        leaveFrom="opacity-100"
                        leaveTo="opacity-0"
                    >
                        <div className="fixed inset-0 bg-black bg-opacity-25" />
                    </Transition.Child>

                    <div className="fixed inset-0 w-screen h-screen overflow-hidden px-16 py-8 flex justify-center z-50 bg-zinc-900 bg-opacity-80">
                        <div
                            className="absolute top-0 left-0 cursor-pointer h-12 w-12 flex items-center justify-center text-4xl drop-shadow"
                            onClick={closeModal}
                        >
                            <svg stroke="currentColor" fill="none" strokeWidth="2" viewBox="0 0 24 24" strokeLinecap="round" strokeLinejoin="round" height="1em" width="1em" xmlns="http://www.w3.org/2000/svg">
                                <line x1="18" y1="6" x2="6" y2="18"></line><line x1="6" y1="6" x2="18" y2="18"></line>
                            </svg>
                        </div>

                        <div className="flex flex-col overflow-hidden rounded-lg border border-zinc-700 box-content">
                            <Transition.Child
                                as={Fragment}
                                enter="ease-out duration-300"
                                enterFrom="opacity-0 scale-95"
                                enterTo="opacity-100 scale-100"
                                leave="ease-in duration-200"
                                leaveFrom="opacity-100 scale-100"
                                leaveTo="opacity-0 scale-95">

                                <Dialog.Panel className="flex w-full h-full bg-white self-stretch flex-col md:flex-row pb-16 md:pb-0 md:pt-0 flex-1">
                                    <div className="mt-2 w-full flex-shrink-0 overflow-hidden text-base px-5 flex flex-col h-auto "
                                        style={{ height: "fit-content", width: "400px" }}>
                                        <div className="mt-6 px-4 py-3 bg-gray-400 rounded-xl shadow bg-opacity-50 font-light flex flex-col space-y-5">
                                            <p>
                                                {(selectedImage?.prompt !== "" && selectedImage?.prompt !== undefined)
                                                    ? selectedImage?.prompt
                                                    : "N/A"}
                                            </p>
                                        </div>
                                        <div className="md:mt-6 mt-4 opacity-100 ml-2 grid grid-cols-2 gap-2 md:flex flex-wrap md:flex-col md:space-x-0 md:space-y-1 h-auto pb-8">
                                            <div>
                                                <div className="text-lg opacity-100">Model name</div>
                                                <div className="text-base opacity-70">{selectedImage?.model_name ? selectedImage?.model_name : "N/A"}</div>
                                            </div>
                                            <div>
                                                <div className="text-lg opacity-100">Seed</div>
                                                <div className="text-base opacity-70">{selectedImage?.seed ? selectedImage?.seed : "N/A"}</div>
                                            </div>
                                            <div>
                                                <div className="text-lg opacity-100">Guidance scale</div>
                                                <div className="text-base opacity-70">{selectedImage?.cfg_scale ? selectedImage?.cfg_scale : "N/A"}</div>
                                            </div>
                                            <div>
                                                <div className="text-lg opacity-100">Dimensions</div>
                                                <div className="text-base opacity-70">{(selectedImage?.height && selectedImage?.width)
                                                    ? (String(selectedImage?.height) + " x " + String(selectedImage?.width))
                                                    : "N/A"}</div>
                                            </div>
                                        </div>

                                    </div>

                                    <div className="mt-2 relative px-5" style={{
                                        maxHeight: "calc(((100vh - 64px) - 50px) - 80px)",
                                        maxWidth: "calc((((100vw - 384px) - 64px) - 150px) - 32px)",
                                        width: "512px",
                                        height: "512px",
                                        marginBottom: "10px",
                                        marginTop: "20px"
                                    }}>
                                        <img alt=""
                                            className='mr-4'
                                            src={selectedImage?.src}
                                            style={{ width: "100%", height: "100%", objectFit: "scale-down" }}>
                                        </img>
                                    </div>
                                </Dialog.Panel>


                            </Transition.Child>
                        </div>
                    </div>
                </Dialog>
            </Transition>
        </>
    )
}
