import axios from "axios";
import { zip } from 'lodash';
import { useContext, useState } from "react";
import { Gallery } from "react-grid-gallery";
import { AppContext } from "@context/AppContext";
import { toast } from "react-toastify";
import { LoadingIndicator } from "@components/LoadingIndicator";
import SearchBar from "@components/SearchBar";
import MasonryListing from "@components/MasonryListing";

import LexicaSearchService from "@services/LexicaSearch";
import ReplicateInferenceService from "@services/ReplicateDiffusionInference";
import { ReplicateSDModelList } from "@config/const";
import { isReplicateResponse } from "@models/ReplicateModel";

// import { SampleImages as IMAGES } from "./SampleImages";
import SampleCats from "./SampleCats";
import { isLexicaResponse } from "@models/LexicaModel";
import IImage from "@models/IImage";
import StyleSuggestion from "./StyleSuggestion";

const HomePage = () => {

    const {
        setIsOpenModal,
        setSelectedImage,
        isLoadingLexica, setIsLoadingLexica,
        isLoadingSD, setIsLoadingSD
    } = useContext(AppContext);

    const [prompt, setPrompt] = useState<string>("");
    
    const randomChoice = (data: string[]) => data[Math.floor(Math.random() * data.length)];
    
    let id: number = 0;
    
    const [items] = useState(() =>
        Array.from(Array(500), () => ({
            id: id++,
            name: `cat ${id++}`,
            src: randomChoice(SampleCats)
        }))
    );

    // Lexica 
    const [displayLexicaImages, setDisplayLexicaImages] = useState<IImage[] | null>(null);

    // Stable Diffusion
    const [displaySDImages, setDisplaySDImages] = useState<IImage[] | null>(null);

    // Styles
    const [suggestStyles, setSuggestStyles] = useState<{ [key: string]: string }>({})


    const handlePromptChange = (event: any) => {
        if (event.key !== "Enter") setPrompt(event.target.value)
    }

    const handleSearchParallel = async () => {
        // TODO: cancel previous inferences if user search a new prompt  

        setIsLoadingLexica(true);
        setIsLoadingSD(true);

        // Priority result from Lexica first 
        setDisplayLexicaImages([]);
        let result_lexica = await LexicaSearchService(prompt);
        if (!isLexicaResponse(result_lexica)) {
            setDisplayLexicaImages(result_lexica);

            setSuggestStyles(StyleSuggestion(result_lexica));
        }
        setIsLoadingLexica(false);

        // After that, get result from SD API all same time
        setDisplaySDImages([]);
        axios.all([
            ReplicateInferenceService(prompt,
                ReplicateSDModelList.OPEN_MIDJOURNEY.model,
                ReplicateSDModelList.OPEN_MIDJOURNEY.version,
                1, undefined, "Open Midjourney", false),
            ReplicateInferenceService(prompt,
                ReplicateSDModelList.STABLE_DIFFUSION.model,
                ReplicateSDModelList.STABLE_DIFFUSION.version,
                1, undefined, "Stable Diffusion", false),

            // SDInferenceService(prompt, APIDomain.RUNPOD_SD_API_7860, "Midjourney SD success", "Midjourney", false),
            // SDInferenceService(prompt, APIDomain.RUNPOD_SD_API_7861, "Stable Diffusion v1.5 success", "SD v1.5", false),
            // SDInferenceService(prompt, APIDomain.RUNPOD_SD_API_7862, "Stable Diffusion v2.0 success", "SD v2.0", false),
            // SDInferenceService(prompt, APIDomain.RUNPOD_SD_API_7863, "Stable Diffusion v2.1 success", "SD v2.1", false),
        ]).then(axios.spread((...res) => {
            // Resolve response from multippe APIs 
            // TODO: re-write resolve into a function 
            let errorCount = 0;
            let result_sd: any[] = [];
            for (let idx = 0; idx < res.length; idx++) {
                if (!(isReplicateResponse(res[idx]))) {
                    result_sd = result_sd.concat(res[idx]);
                    setDisplaySDImages(result_sd);
                }
                else {
                    console.log("Error result : ", res[idx])
                    errorCount += 1;
                }
            }

            // if all Diffusion API failed then notify user
            (errorCount === res.length)
                ? toast.error("Unable to inference Stable Diffusion")
                : toast.success("Inference Stable Diffusion successful");

            setIsLoadingSD(false);

        })).catch((error) => {
            toast.error("Unknown error, please contact developer...");
            console.log(error);
        });
    }

    return (
        <div>
            <SearchBar onSubmit={handleSearchParallel} onChange={handlePromptChange} value={prompt} />
            {/* Style suggestion */}
            <div className="flex justify-center mb-4">
                <div className="ml-4 mr-4 space-x-2 grid grid-cols-5 grid-flow-col gap-2">
                    {(Object.entries(suggestStyles).length !== 0) && zip(
                        [
                            "text-white bg-gradient-to-r from-blue-500 via-blue-600 to-blue-700 hover:bg-gradient-to-br focus:ring-4 focus:outline-none focus:ring-blue-300 dark:focus:ring-blue-800 font-medium rounded-lg text-sm px-5 py-2.5 text-center mr-2 mb-2",
                            "text-white bg-gradient-to-r from-green-400 via-green-500 to-green-600 hover:bg-gradient-to-br focus:ring-4 focus:outline-none focus:ring-green-300 dark:focus:ring-green-800 font-medium rounded-lg text-sm px-5 py-2.5 text-center mr-2 mb-2",
                            "text-white bg-gradient-to-r from-cyan-400 via-cyan-500 to-cyan-600 hover:bg-gradient-to-br focus:ring-4 focus:outline-none focus:ring-cyan-300 dark:focus:ring-cyan-800 font-medium rounded-lg text-sm px-5 py-2.5 text-center mr-2 mb-2",
                            "text-white bg-gradient-to-r from-pink-400 via-pink-500 to-pink-600 hover:bg-gradient-to-br focus:ring-4 focus:outline-none focus:ring-pink-300 dark:focus:ring-pink-800 font-medium rounded-lg text-sm px-5 py-2.5 text-center mr-2 mb-2",
                            "text-white bg-gradient-to-r from-purple-500 via-purple-600 to-purple-700 hover:bg-gradient-to-br focus:ring-4 focus:outline-none focus:ring-purple-300 dark:focus:ring-purple-800 font-medium rounded-lg text-sm px-5 py-2.5 text-center mr-2 mb-2"
                        ],
                        Object.entries(suggestStyles)).map(([color, style_key]) => (
                            <button
                                key={style_key?.[0]}
                                type="button"
                                className={color}
                                onClick={() => {
                                    setPrompt(prompt + " " + style_key?.[1])
                                }}>
                                {style_key?.[0]}
                            </button>
                        ))
                    }
                </div>
            </div>

            {!(displayLexicaImages || displaySDImages) &&
                <div>
                    {/* <h2 className=" text-2xl ml-4 mb-2 text-left opacity-100">Random Images</h2> */}
                    {/* <Gallery
                        images={IMAGES}
                        onClick={(e) => {
                            setSelectedImage(IMAGES[e]);
                            setIsOpenModal(true);
                        }}
                    /> */}
                    <MasonryListing items={items} />
                </div>}

            {(!isLoadingLexica && (displayLexicaImages !== null))
                ? <div className="bg-zinc-200">
                    <h2 className=" text-2xl ml-4 mb-2 text-left opacity-100">
                        Similar result</h2>
                    <Gallery
                        images={displayLexicaImages}
                        onClick={(e) => {
                            setSelectedImage(displayLexicaImages[e]);
                            setIsOpenModal(true);
                        }}
                    />
                </div>
                : isLoadingLexica && LoadingIndicator()}

            {(!isLoadingSD && (displaySDImages !== null))
                ? <div className="bg-blue-100 mt-4">
                    <h2 className=" ml-4 text-left text-2xl opacity-100">Inference Result</h2>
                    <Gallery
                        images={displaySDImages}
                        onClick={(e) => {
                            setSelectedImage(displaySDImages[e]);
                            setIsOpenModal(true);
                        }}
                    />
                </div>
                : isLoadingSD && LoadingIndicator()}

        </div >
    )
}

export default HomePage
